html, body {
  height: 100vh;
  min-width: 330px;
  font-size: 16px;
  background-image: linear-gradient(white, #b8b7b7);
  margin: 0;
  padding: 0;
}

.App {
  position: relative;
  font-family: Arial, Helvetica, sans-serif;
}

.app__header {
  background-image: linear-gradient(#3A3A3A, #202020);
  display: flex;
  position: relative;
  justify-items: center;
  height: 80px;
  width: 100%;
  min-width: 330px;
  border-bottom: solid 3px black;
  padding-left: 15px;
  z-index: 2;
}

.app__content {
  height: calc(100vh - 80px);
  padding: 10px 10px 0 10px;
  position: relative;
  align-self: stretch;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.app__container-buttons {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}

@media all and (max-width: 1500px) {
  .app__header {
    height: 70px;
    padding-left: 10px;
  }
  .app__content {
    height: calc(100vh - 70px);
    padding: 5px 5px 0 5px;
  }
  .app__container-buttons {
    margin-bottom: 8px;
  }
}

@media all and (max-width: 1250px) {
  .app__header {
    height: 55px;
    padding-left: 5px;
  }
  .app__content {
    height: calc(100vh - 55px);
    padding-top: 3px;
  }
  .app__container-buttons {
    margin-bottom: 5px;
  }
}

@media all and (max-width: 800px) {
  .app__header {
    height: 45px;
  }
  .app__content {
    height: calc(100vh - 45px);
    padding: 2px 2px 0 2px;
  }
  .app__container-buttons {
    font-size: 0.5rem;
  }
}

@media all and (max-width: 650px) {
  .app__header {
    height: 35px;
  }
  .app__content {
    height: calc(100vh - 35px);
  }
}

*::-webkit-scrollbar {
  position: absolute;
  width: 5px;
}

*::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px black;
}

*::-webkit-scrollbar-thumb {
  border: 1px solid black;
  background-color: #312929;
  outline: 2px solid black;
}

.invisible {
  visibility: hidden;
  z-index: -1;
}

.display-none {
  display: none;
}

.display-block {
  display: block;
}

.display-flex {
  display: flex !important;
}

.position-absolute-center-center {
  position: fixed !important;
  top: 50% !important;
  left: 50% !important;
  transform: translate(-50%, -50%) !important;
}

.common-cursor-pointer {
  cursor: pointer;
}

.common-customCheckBox {
  background: transparent;
  border: none;
}

.warning-urgetTask {
  width: 35px;
  height: 45px;
  position: absolute;
  bottom: 20px;
  left: 20px;
  border-radius: 5px;
  background-color: red;
  color: white;
  text-align: center;
  font-size: 30px;
  animation: blinking-warning 0.5s linear infinite;
  z-index: 10;
}

@keyframes blinking-warning {
  0% {
    background: #CC0000;
  }
  100% {
    background: #e97d88;
  }
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.common__loader_box {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%);
}

.common__loader_box_loader {
  border: 16px solid #f3f3f3;
  border-top: 16px solid #332c2c;
  border-radius: 50%;
  width: 100px;
  height: 100px;
  animation: spin 2s linear infinite;
}

@media all and (max-width: 1250px) {
  .common__loader_box_loader {
    border-width: 14px;
    border-top-width: 14px;
    width: 85px;
    height: 85px;
  }
}

@media all and (max-width: 800px) {
  .common__loader_box_loader {
    border-width: 12px;
    border-top-width: 12px;
    width: 70px;
    height: 70px;
  }
}

@media all and (max-width: 650px) {
  .common__loader_box_loader {
    border-width: 10px;
    border-top-width: 10px;
    width: 60px;
    height: 60px;
